import tabBar from "../../components/tab-bar/tabBar.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue"
export default {
  data() {
    return {
      showSignModel:false
    };
  },
  methods: {
    switchSignModel(){
      this.showSignModel=!this.showSignModel
    },
    toBalance(){
      uni.navigateTo({
        url: "/pages/balance/balance"
      });
    },
    toRecharge(){
      uni.navigateTo({
        url: "/pages/recharge/recharge"
      });
    },
    toMyYuYue(){
      uni.navigateTo({
        url: "/pages/my_yuyue/my_yuyue"
      });
    },
    toIntegral(){
      uni.navigateTo({
        url: "/pages/integral/integral"
      })
    },
    toInvitation(){
      uni.navigateTo({
        url: "/pages/invitation/invitation"
      });
    },
    toViolation(){
      uni.navigateTo({
        url: "/pages/violation/violation"
      });
    },
    toMyCar(){
      uni.navigateTo({
        url: "/pages/my_car/my_car"
      });
    }
  },
  components: { tabBar ,uniPopup},
  onLoad: function() {},
  onShow: function() {
    uni.hideTabBar();
  },
  onHide: function() {}
};
